@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
html, body {
  margin: 0;
  padding:0;
  min-height:100%;
}

html {
  font-size: 15px;
  font-weight: 500;
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
  background-color: var(--background-color);
  color: #202124;
  height:100vh;
}

h2 {
    font-size: 16px;
    -webkit-font-smoothing: antialiased;
    letter-spacing: 0;
    line-height: 22px;
    font-weight: 600;
    text-rendering: optimizeLegibility;
}

@media (min-width: 50em){
    h2 {
        font-size: 24px;
        line-height: 30px;
    }
}
.description {
    font-size: 14px;
    line-height: 18px;
}

@media (min-width: 50em) {
    .description {
        font-size: 16px;
    }    
}
:root {
    --primary-color: #fff;
    --background-color: #f8f9fa;
    --bright-border-color: #e8eaed;
    --card-border-color: #dadce0;
    --filter-table: hsl(180, 31%, 95%);
    --light-grey: #e7e9ea;
    --darker-grey: #f8f8f8;
    --links-blue: #1967d2;
    --input-green: #80868b;
    --dark-greyish: hsl(180, 8%, 52%);
    --darker-greyish: #5f6368;
    --very-dark-greyish: hsl(180, 14%, 20%);
    --letter-color-black: #202124;
  }
.article-action{
    margin-top: 20px;
    cursor: pointer;
    color: var(--links-blue);
    background: transparent;
    border: 0;
    cursor: pointer;
    display: inline-block;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.15;
    text-align: center;
}
.banner {
    width: 100%;
    height: 8rem;
    font-size: 20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;

    background-color: var(--letter-color-black);
    color: var(--primary-color)
}

@media (min-width: 50em) {    
    .banner {
        height: 5rem;
    }
}
.page-body-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

.page-body {
    padding-top: 2rem;
}

.article-error > .description {
    margin-top: 30px;
}
.title {
    -webkit-box-orient: vertical;
    display: -webkit-box;
    font-size: 14px;
    -webkit-line-clamp: 2;
    line-height: 23px;
    margin-bottom: 9px;
    margin-top: 0;
    overflow: hidden;


    font-size: 16px;
    -webkit-font-smoothing: antialiased;
    letter-spacing: 0;
    line-height: 22px;
    font-weight: 600;
    text-rendering: optimizeLegibility;
}

.title-small {
    -webkit-box-orient: vertical;
    display: -webkit-box;
    font-size: 14px;
    -webkit-line-clamp: 2;
    line-height: 23px;
    margin-bottom: 9px;
    margin-top: 0;
    overflow: hidden;
}

@media (min-width: 50em){
    .title {
        font-size: 16px;
        line-height: 30px;

        font-size: 24px;
        line-height: 30px;
    }

    .title-small {
        font-size: 16px;
        line-height: 20px;
        font-weight: 400;
    }
}
.subtitle {
    font-style: italic;
    margin-top: 30px;
}

@media (min-width: 50em) {
    .subtitle {
        margin-top: 30px;
    }
}
.article-header-wrapper {
    color: var(--letter-color-black);
    padding: 0 30px;
}

.article-header {
    padding: 10px 0;
}

@media (min-width: 50em) {
    .article-header-wrapper {
        padding: 30px;
    }

    .article-header {
        margin-top: 30px;
    }
}
.article-body {
    padding: 0 30px;
}
.page-body-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

.page-body {
    padding-top: 2rem;
}

.loder{
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: var(--letter-color-black);
}

.lds-ellipsis {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%)
}
.lds-ellipsis div {
position: absolute;
top: 33px;
width: 13px;
height: 13px;
border-radius: 50%;
background: #fff;
-webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
        animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
left: 8px;
-webkit-animation: lds-ellipsis1 0.6s infinite;
        animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
left: 8px;
-webkit-animation: lds-ellipsis2 0.6s infinite;
        animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
left: 32px;
-webkit-animation: lds-ellipsis2 0.6s infinite;
        animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
left: 56px;
-webkit-animation: lds-ellipsis3 0.6s infinite;
        animation: lds-ellipsis3 0.6s infinite;
}

@-webkit-keyframes lds-ellipsis1 {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes lds-ellipsis1 {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
@-webkit-keyframes lds-ellipsis3 {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
}
@keyframes lds-ellipsis3 {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
}
@-webkit-keyframes lds-ellipsis2 {
    0% {
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
    100% {
        -webkit-transform: translate(24px, 0);
                transform: translate(24px, 0);
    }
}
@keyframes lds-ellipsis2 {
    0% {
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
    100% {
        -webkit-transform: translate(24px, 0);
                transform: translate(24px, 0);
    }
}

.search-button {
    height: 60px;
    border:  1px solid var(--card-border-color);
    background: var(--primary-color);
    text-align: center;
    color: var(--darker-greyish);
    cursor: pointer;
    font-size: 20px;
    border-left: 0;
    padding-right: 30px;
}
.search-term {
  outline: none;
  padding-left: 30px;
  background-color: var(--primary-color);
  border: 1px solid var(--card-border-color);
  display: block;
  height: 56px;
  width: 100%;
  border-right: 0;
}

.search-term:focus{
color:  var(--letter-color-black);
}
.search {
  width: 100%;
  position: relative;
  display: flex;
  margin: 1.3rem 0 0;
}

.search-wrapper {
  padding: 0 30px;
}
@media (min-width: 50em){
  .search-wrapper > h2 {
    font-size: 20px
  }
}
.card-header {
    color: var(--letter-color-black);
    margin-left: 18px;
    margin-right: 18px;
    padding: 0 18px;
    position: relative;
    width: 100%;
    flex: 1 1;
    padding: 12px 0 8px;
}

@media (min-width: 50em) {
    .card-header {
        margin-left: 30px;
        margin-right: 30px;
        padding: 18px 0 8px;
    }
}
.card-wrapper {
    background: var(--primary-color);
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    border: 1px solid var(--card-border-color);

    padding: 0 2rem;
    position: relative;
}

.card-wrapper:first-child {
    margin: 0;
}

@media (min-width: 50em) {
    .card-wrapper {
        font-size: 16px;
        line-height: 28px;
    }

    .card-wrapper:hover {
        background-color: var(--light-grey);
    }
}
.articles-list {
    display: flex;
    flex-direction: column;

    padding: 10px 30px;
    flex: 1 1;
}

@media (min-width: 50em) {
    .articles-list {
        padding: 30px;
    }

    .articles-list > h2 {
        font-size: 20px;
    }
}

.empty-list {
    display: flex;
    -ms-grid-row-align: center;
    align-self: center;
    flex-direction: column;
    align-items: center;
}
.pagination {
    display: flex;
    justify-content: space-between;
    padding: 0 30px;
    padding-bottom: 30px;
}
.page-body-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}


.page-body {
    padding-top: 2rem;
}

